* {
  margin: 0;
  box-sizing: border-box;
}

.body {
  background-color: black;
}

.wrapper {
  display: grid;
  grid-template-columns: repeat(2, 700px);
  grid-template-rows: repeat(10, 350px);
  column-gap: 30px;
  row-gap: 50px;
  justify-content: center;
  padding-top: 50px;
}
